
import { Link } from 'react-router-dom';

const Navbar = () => {
  return (
    <nav className="bg-soft-peach shadow-sm fixed w-full z-10">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex-shrink-0 flex items-center">
            <Link to="/" className="font-playfair text-2xl font-bold text-primary">Dr. Smith</Link>
          </div>
          <div className="flex items-center space-x-8">
            <Link to="/" className="text-gray-700 hover:text-primary transition">Home</Link>
            <Link to="/about" className="text-gray-700 hover:text-primary transition">About Us</Link>
            <Link to="/therapy" className="text-gray-700 hover:text-primary transition">Occupational Therapy</Link>
            <Link to="/contact" className="text-gray-700 hover:text-primary transition">Contact</Link>
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;
