
import { Button } from "@/components/ui/button";
import { useNavigate } from "react-router-dom";
import { Check, Users } from "lucide-react";

const Home = () => {
  const navigate = useNavigate();

  return (
    <div>
      <section className="bg-soft-peach py-20">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h1 className="text-4xl md:text-5xl font-playfair font-bold mb-6">Dr. Sarah Smith</h1>
            <p className="text-xl text-gray-600 mb-8">Pediatric Occupational Therapy Services</p>
            <Button onClick={() => navigate('/contact')} size="lg" className="bg-primary hover:bg-primary/90">
              Schedule a Consultation
            </Button>
          </div>
        </div>
      </section>

      <section className="py-16 bg-soft-green">
        <div className="max-w-3xl mx-auto px-4 sm:px-6 lg:px-8 text-center">
          <h2 className="text-3xl font-playfair font-bold mb-6">Our Mission</h2>
          <p className="text-lg text-gray-600">
            "As a pediatric occupational therapist, my mission is to help every child reach their full potential. 
            I believe in creating a nurturing environment where children can develop essential life skills through 
            engaging and purposeful activities. Together, we can help your child thrive."
          </p>
        </div>
      </section>

      <section className="py-16 bg-soft-yellow">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-3xl font-playfair font-bold text-center mb-12">Why Choose Us?</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {features.map((feature, index) => (
              <div key={index} className="bg-white p-6 rounded-lg shadow-sm">
                <div className="flex items-center mb-4">
                  <Check className="w-6 h-6 text-primary mr-2" />
                  <h3 className="font-semibold text-lg">{feature.title}</h3>
                </div>
                <p className="text-gray-600">{feature.description}</p>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className="py-16 bg-soft-purple">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <h2 className="text-3xl font-playfair font-bold text-center mb-12">What Parents Say</h2>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
            {testimonials.map((testimonial, index) => (
              <div key={index} className="bg-white p-6 rounded-lg">
                <p className="text-gray-600 mb-4 italic">"{testimonial.text}"</p>
                <div className="flex items-center">
                  <Users className="w-5 h-5 text-primary mr-2" />
                  <p className="font-semibold">{testimonial.author}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      <section className="py-16 bg-soft-pink">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="text-center">
            <h2 className="text-3xl font-playfair font-bold mb-6">Ready to Get Started?</h2>
            <p className="text-lg text-gray-600 mb-8 max-w-2xl mx-auto">
              Take the first step towards helping your child develop essential life skills. 
              Contact us today to schedule a consultation.
            </p>
            <Button onClick={() => navigate('/contact')} size="lg" className="bg-primary hover:bg-primary/90">
              Contact Us
            </Button>
          </div>
        </div>
      </section>
    </div>
  );
};

const features = [
  {
    title: "Personalized Care",
    description: "Every child is unique. We create customized therapy plans tailored to your child's specific needs and goals."
  },
  {
    title: "Expert Guidance",
    description: "With years of specialized experience in pediatric occupational therapy, we provide professional and compassionate care."
  },
  {
    title: "Collaborative Approach",
    description: "We work closely with families, teachers, and other healthcare providers to ensure comprehensive support for your child."
  }
];

const testimonials = [
  {
    text: "Dr. Smith has been amazing with our daughter. We've seen incredible progress in just a few months.",
    author: "Sarah M."
  },
  {
    text: "The personalized attention and care our son receives is outstanding. Highly recommend!",
    author: "John D."
  },
  {
    text: "Dr. Smith's approach is both professional and caring. She truly understands children's needs.",
    author: "Emily R."
  }
];

export default Home;
