
import { Link } from 'react-router-dom';
import { Mail, Phone, Facebook, Instagram, Linkedin } from 'lucide-react';

const Footer = () => {
  return (
    <footer className="bg-soft-blue pt-16 pb-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <div>
            <h3 className="text-lg font-semibold mb-4">Navigation</h3>
            <div className="space-y-3">
              <Link to="/" className="block text-gray-600 hover:text-primary">Home</Link>
              <Link to="/about" className="block text-gray-600 hover:text-primary">About Us</Link>
              <Link to="/therapy" className="block text-gray-600 hover:text-primary">Occupational Therapy</Link>
            </div>
          </div>
          <div>
            <h3 className="text-lg font-semibold mb-4">Contact</h3>
            <div className="space-y-3">
              <a href="tel:+1234567890" className="flex items-center text-gray-600 hover:text-primary">
                <Phone className="w-5 h-5 mr-2" />
                (123) 456-7890
              </a>
              <a href="mailto:info@drsmith.com" className="flex items-center text-gray-600 hover:text-primary">
                <Mail className="w-5 h-5 mr-2" />
                info@drsmith.com
              </a>
            </div>
          </div>
          <div>
            <h3 className="text-lg font-semibold mb-4">Social</h3>
            <div className="flex space-x-4">
              <a href="https://facebook.com" className="text-gray-600 hover:text-primary">
                <Facebook className="w-6 h-6" />
              </a>
              <a href="https://instagram.com" className="text-gray-600 hover:text-primary">
                <Instagram className="w-6 h-6" />
              </a>
              <a href="https://linkedin.com" className="text-gray-600 hover:text-primary">
                <Linkedin className="w-6 h-6" />
              </a>
            </div>
          </div>
        </div>
        <div className="mt-8 pt-8 border-t border-gray-200">
          <p className="text-center text-gray-500">&copy; {new Date().getFullYear()} Dr. Smith Pediatric Occupational Therapy. All rights reserved.</p>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
